import React, { useState, useEffect } from 'react';
import {
  Card,
  Flex,
  Text,
  Heading,
  Badge,
  Button,
  Alert,
  Loader,
  Divider,
  Tabs,
  TabItem,
  View,
  SelectField,
  useTheme
} from '@aws-amplify/ui-react';
import { requestApi, adsTxtApi } from '../../api';
import { RequestWithRecords, AdsTxtRecord, Message } from '../../models';
import AdsTxtRecordList from '../adsTxt/AdsTxtRecordList';
import MessageList from '../messages/MessageList';
import MessageForm from '../messages/MessageForm';

interface RequestDetailProps {
  requestId: string;
  token: string;
}

const RequestDetail: React.FC<RequestDetailProps> = ({ requestId, token }) => {
  const [request, setRequest] = useState<RequestWithRecords | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [adsTxtContent, setAdsTxtContent] = useState<string>('');
  const [showAdsTxtContent, setShowAdsTxtContent] = useState(false);
  const [activeTab, setActiveTab] = useState(0);
  const { tokens } = useTheme();

  const fetchRequestDetails = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await requestApi.getRequest(requestId, token);
      
      if (response.success) {
        setRequest(response.data);
      } else {
        setError(response.error?.message || '');
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const fetchMessages = async () => {
    try {
      const response = await adsTxtApi.getRecordsByRequestId(requestId, token);
      if (response.success) {
        // Messages are already in the request
      }
    } catch (err) {
      console.error(':', err);
    }
  };

  const generateAdsTxtContent = async () => {
    try {
      const content = await adsTxtApi.generateAdsTxtContent(requestId, token);
      setAdsTxtContent(content);
      setShowAdsTxtContent(true);
    } catch (err) {
      console.error('Ads.txt:', err);
    }
  };

  const handleStatusChange = async (newStatus: 'pending' | 'approved' | 'rejected') => {
    if (!request) return;
    
    try {
      setLoading(true);
      
      const response = await requestApi.updateRequestStatus(requestId, newStatus, token);
      
      if (response.success) {
        // Update the local state with the new status
        setRequest(prev => prev ? {
          ...prev,
          request: {
            ...prev.request,
            status: newStatus
          }
        } : null);
      } else {
        setError(response.error?.message || '');
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleRecordStatusChange = async (recordId: string, status: 'pending' | 'approved' | 'rejected') => {
    try {
      setLoading(true);
      
      const response = await adsTxtApi.updateRecordStatus(recordId, status, token);
      
      if (response.success) {
        // Update the local state with the new record status
        setRequest(prev => {
          if (!prev) return null;
          
          return {
            ...prev,
            records: prev.records.map(record => 
              record.id === recordId 
                ? { ...record, status } 
                : record
            )
          };
        });
      } else {
        setError(response.error?.message || '');
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const handleMessageSent = (message: Message) => {
    setMessages(prev => [...prev, message]);
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'approved':
        return <Badge variation="success"></Badge>;
      case 'rejected':
        return <Badge variation="error"></Badge>;
      case 'pending':
        return <Badge variation="warning"></Badge>;
      case 'updated':
        return <Badge variation="info"></Badge>;
      default:
        return <Badge>{status}</Badge>;
    }
  };

  useEffect(() => {
    fetchRequestDetails();
    fetchMessages();
  }, [requestId, token]);

  if (loading && !request) {
    return (
      <Flex justifyContent="center" padding="2rem">
        <Loader size="large" />
      </Flex>
    );
  }

  if (error) {
    return (
      <Alert variation="error">
        {error}
      </Alert>
    );
  }

  if (!request) {
    return (
      <Alert variation="warning">
        ID
      </Alert>
    );
  }

  const approvedRecords = request.records.filter(record => record.status === 'approved');
  const pendingRecords = request.records.filter(record => record.status === 'pending');
  const rejectedRecords = request.records.filter(record => record.status === 'rejected');

  return (
    <Card padding="1.5rem" variation="outlined">
      <Flex direction="column" gap="1.5rem">
        <Flex justifyContent="space-between" alignItems="center">
          <Heading level={2}></Heading>
          {getStatusBadge(request.request.status)}
        </Flex>
        
        <Divider />
        
        <Flex direction="column" gap="1rem">
          <Heading level={3}></Heading>
          
          <Flex gap="1rem" wrap="wrap">
            <Card variation="outlined" padding="1rem" flex="1" minWidth="250px">
              <Heading level={5}></Heading>
              <Text>
                <strong>:</strong> {request.request.publisher_email}
              </Text>
              {request.request.publisher_name && (
                <Text>
                  <strong>:</strong> {request.request.publisher_name}
                </Text>
              )}
              {request.request.publisher_domain && (
                <Text>
                  <strong>:</strong> {request.request.publisher_domain}
                </Text>
              )}
            </Card>
            
            <Card variation="outlined" padding="1rem" flex="1" minWidth="250px">
              <Heading level={5}></Heading>
              <Text>
                <strong>:</strong> {request.request.requester_email}
              </Text>
              <Text>
                <strong>:</strong> {request.request.requester_name}
              </Text>
            </Card>
          </Flex>
          
          <Flex gap="1rem" wrap="wrap">
            <Card variation="outlined" padding="1rem" flex="1" minWidth="250px">
              <Heading level={5}></Heading>
              <Text>
                <strong>ID:</strong> {request.request.id}
              </Text>
              <Text>
                <strong>:</strong> {new Date(request.request.created_at).toLocaleString()}
              </Text>
              <Text>
                <strong>:</strong> {new Date(request.request.updated_at).toLocaleString()}
              </Text>
            </Card>
            
            <Card variation="outlined" padding="1rem" flex="1" minWidth="250px">
              <Heading level={5}></Heading>
              <Text>
                <strong>:</strong> {request.records.length}
              </Text>
              <Text>
                <strong>:</strong> {approvedRecords.length}
              </Text>
              <Text>
                <strong>:</strong> {pendingRecords.length}
              </Text>
              <Text>
                <strong>:</strong> {rejectedRecords.length}
              </Text>
            </Card>
          </Flex>
          
          {request.request.status === 'pending' && (
            <Flex gap="1rem" marginTop="1rem">
              <Button
                variation="primary"
                onClick={() => handleStatusChange('approved')}
                isLoading={loading}
                flex="1"
              >
                
              </Button>
              <Button
                variation="destructive"
                onClick={() => handleStatusChange('rejected')}
                isLoading={loading}
                flex="1"
              >
                
              </Button>
            </Flex>
          )}
        </Flex>
        
        <Divider />
        
        <Tabs
          currentIndex={activeTab}
          onChange={index => setActiveTab(typeof index === 'number' ? index : 0)}
        >
          <TabItem title="Ads.txt">
            <View padding="1rem">
              <AdsTxtRecordList
                records={request.records}
                onStatusChange={handleRecordStatusChange}
                isEditable={request.request.status === 'pending'}
              />
              
              {approvedRecords.length > 0 && (
                <Button
                  onClick={generateAdsTxtContent}
                  marginTop="1rem"
                >
                  Ads.txt
                </Button>
              )}
              
              {showAdsTxtContent && (
                <Card 
                  variation="outlined" 
                  padding="1rem" 
                  marginTop="1rem" 
                  backgroundColor={tokens.colors.background.secondary}
                >
                  <Flex justifyContent="space-between" alignItems="center" marginBottom="0.5rem">
                    <Heading level={4}>Ads.txt</Heading>
                    <Button
                      size="small"
                      onClick={() => {
                        navigator.clipboard.writeText(adsTxtContent);
                      }}
                    >
                      
                    </Button>
                  </Flex>
                  <pre style={{ 
                    whiteSpace: 'pre-wrap', 
                    fontFamily: 'monospace',
                    overflow: 'auto',
                    maxHeight: '300px'
                  }}>
                    {adsTxtContent}
                  </pre>
                </Card>
              )}
            </View>
          </TabItem>
          
          <TabItem title="">
            <View padding="1rem">
              <MessageList
                messages={messages}
                requestId={requestId}
                token={token}
              />
              
              <Divider marginBlock="1rem" />
              
              <MessageForm
                requestId={requestId}
                token={token}
                onMessageSent={handleMessageSent}
              />
            </View>
          </TabItem>
        </Tabs>
      </Flex>
    </Card>
  );
};

export default RequestDetail;