import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import {
  Card,
  Flex,
  Heading,
  View,
  Text,
  Button,
  TextField,
  Alert,
  Divider
} from '@aws-amplify/ui-react';
import { requestApi } from '../api';
import { useApp } from '../context/AppContext';

const HomePage: React.FC = () => {
  const [email, setEmail] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();

  const handleEmailSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!email) {
      setError('');
      return;
    }

    try {
      setIsLoading(true);
      setError(null);
      
      // Fetch requests for this email
      const response = await requestApi.getRequestsByEmail(email);
      
      if (response.success && response.data.length > 0) {
        // If requests exist, navigate to the request list
        navigate(`/requests?email=${encodeURIComponent(email)}`);
      } else {
        // No requests found, show message
        setError('');
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <View padding={{ base: '1rem', medium: '2rem' }}>
      <Flex direction="column" alignItems="center" gap="2rem">
        <Heading level={1} textAlign="center">
          Ads.txt 
        </Heading>
        
        <Text textAlign="center" maxWidth="800px">
          Ads.txt ManagerAds.txt
          Ads.txt
        </Text>
        
        <Flex
          direction={{ base: 'column', medium: 'row' }}
          gap="2rem"
          width="100%"
          maxWidth="1000px"
          padding="1rem"
        >
          <Card variation="outlined" flex="1">
            <Heading level={3}></Heading>
            <Divider marginBlock="1rem" />
            <Text>
              Ads.txt
              CSV
            </Text>
            <Button
              as={Link}
              to="/new-request"
              variation="primary"
              width="100%"
              marginTop="1rem"
            >
              
            </Button>
          </Card>

          <Card variation="outlined" flex="1">
            <Heading level={3}></Heading>
            <Divider marginBlock="1rem" />
            <Text marginBottom="1rem">
              
              
            </Text>
            
            <form onSubmit={handleEmailSubmit}>
              <Flex direction="column" gap="0.5rem">
                <TextField
                  label=""
                  name="email"
                  placeholder="example@domain.com"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  type="email"
                  required
                />
                {error && (
                  <Alert variation="error">
                    {error}
                  </Alert>
                )}
                <Button
                  type="submit"
                  variation="primary"
                  width="100%"
                  isLoading={isLoading}
                >
                  
                </Button>
              </Flex>
            </form>
          </Card>
        </Flex>
      </Flex>
    </View>
  );
};

export default HomePage;