import React, { useState } from 'react';
import { 
  Card,
  Flex,
  TextField,
  Button,
  Alert,
  Heading
} from '@aws-amplify/ui-react';
import { messageApi } from '../../api';
import { Message } from '../../models';

interface MessageFormProps {
  requestId: string;
  token: string;
  onMessageSent?: (message: Message) => void;
}

const MessageForm: React.FC<MessageFormProps> = ({ requestId, token, onMessageSent }) => {
  const [content, setContent] = useState('');
  const [senderEmail, setSenderEmail] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!content || !senderEmail) {
      setError('');
      return;
    }
    
    try {
      setIsLoading(true);
      setError(null);
      setSuccess(false);
      
      const response = await messageApi.createMessage({
        request_id: requestId,
        sender_email: senderEmail,
        content,
        token
      });
      
      if (response.success) {
        setContent('');
        setSuccess(true);
        
        if (onMessageSent) {
          onMessageSent(response.data);
        }
      } else {
        setError(response.error?.message || '');
      }
    } catch (err) {
      setError('');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Card variation="outlined" padding="1rem">
      <Heading level={4}></Heading>
      
      <form onSubmit={handleSubmit}>
        <Flex direction="column" gap="1rem" marginTop="1rem">
          {error && (
            <Alert variation="error">
              {error}
            </Alert>
          )}
          
          {success && (
            <Alert variation="success">
              
            </Alert>
          )}
          
          <TextField
            label=""
            value={senderEmail}
            onChange={e => setSenderEmail(e.target.value)}
            placeholder="your@email.com"
            type="email"
            isRequired
          />
          
          <TextField
            label=""
            value={content}
            onChange={e => setContent(e.target.value)}
            placeholder="..."
            isRequired
            as="textarea"
            rows={4}
          />
          
          <Button
            type="submit"
            variation="primary"
            isLoading={isLoading}
            isDisabled={!content || !senderEmail}
          >
            
          </Button>
        </Flex>
      </form>
    </Card>
  );
};

export default MessageForm;